.main-menu {
    width: 100%;
    & > ul {
        & > li {
            margin-right: 0;
            position: relative;
            .menu-item {
                cursor: pointer;
            }
            a {
                color: var(--highlight-color);
                font-weight: 500;
                font-size: 18px;
                text-decoration: none;
                padding: 1rem 1.5rem;
                display: inline-block;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
    .sub-menu-wrapper {
        top: 60px;
        filter: drop-shadow(0 -1px 6px rgba(0, 0, 0, 0.15));
        -webkit-filter: drop-shadow(0 -1px 6px rgba(0, 0, 0, 0.15));
        -moz-filter: drop-shadow(0 -1px 6px rgba(0, 0, 0, 0.15));
        background: white;
        opacity: 0;
        visibility: hidden;
        &.open {
            visibility: visible;
            opacity: 1;
        }
        .sub-menu-content {
            ul {
                &::before {
                    border-bottom: 15px solid #fff;
                    border-left: 15px solid transparent;
                    border-right: 15px solid transparent;
                    content: "";
                    height: 0;
                    margin-left: 20px;
                    margin-top: -14px;
                    position: absolute;
                    width: 0;
                    z-index: 0;
                }
                li {
                    border-bottom: 1px solid #bedae3;
                    margin-right: 0;
                    &:last-of-type {
                        border-bottom: 0;
                    }
                    a {
                        width: max-content;
                        padding: 1rem;
                    }
                }
            }
        }
    }
}
.action-menu {
    ul {
        li {
            &.phone {
                a {
                    border: 0;
                    background: none !important;
                    svg {
                        width: 30px;
                        height: 30px;
                    }
                }
            }
            a {
                text-decoration: none;
                color: var(--highlight-color);
                font-weight: 500;
                border-radius: var(--bs-border-radius);
                padding: 1rem 0.75rem;
                display: block;
                border: 2px solid var(--primary-color);
            }
            &:nth-child(1) {
                a {
                    background: var(--primary-color);
                }
            }
        }
    }
}
@media (min-width: 992px) {
    .main-menu {
        width: auto;
        ul {
            li {
                margin-right: 0.5rem;
                &:last-of-type {
                    margin-right: 0;
                    &::after {
                        content: none;
                    }
                }
                a {
                    padding: 0.5rem 0.75rem;
                }
            }
        }
    }
}
